import { SERVER_ASSET } from "config"

export default function OrderDetail(p) {
    return (
        <div className='order-detail'>
            <div className='pd'>
                <div>
                    <h4>Order Information</h4>
                    <div>To: {p.data.fullname} - {p.data.phone}</div>
                    <div>Address: {p.data.address}</div>
                </div>
                <div className="gap"></div>
                <div className="scroll order-info ">
                    <table className="table scroll border align--center">
                        <thead>
                            <tr className="align--center">
                                <th>Product</th>
                                <th>Name</th>
                                <th>Price</th>
                                <th>Amount</th>
                            </tr>
                        </thead>
                        <tbody>
                            {p.data.products.length > 0 && (
                                p.data.products.map((o, i) => {
                                    return (
                                        <tr key={i}>
                                            <td>
                                                <div className="product-img"><img src={SERVER_ASSET + o.product.images[0]} /></div>
                                            </td>
                                            <td className="align--center">
                                                {o.product.name}
                                            </td>
                                            <td className="align--right">
                                                {new Intl.NumberFormat("de-DE").format(o.product.price) + 'đ'}
                                            </td>
                                            <td className="align--right">
                                                {o.amount}
                                            </td>
                                        </tr>
                                    )
                                })
                            )}
                        </tbody>
                    </table>
                </div>
                <div className="gap--20"></div>
                <div className="align--right">
                    Tổng cộng: {p.data.totalPrice.toLocaleString()} VNĐ
                </div>
                <div className="gap"></div>
            </div>
        </div>
    )
}