import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import { useSelector } from "react-redux";
import { SERVER_API, HOME } from "config";
import { toast } from 'react-toastify';
import PageHeader from "components/PageHeader";
import Title from 'components/Title';
import OrderDetail from './OrderDetail';
import Popup from 'UI/Popup';

import './style.scss';
export default function OrderPage() {
    const client = axios.create({ baseURL: SERVER_API });
    const navigate = useNavigate();
    const user = useSelector(state => state.user)

    const [orders, setOrders] = useState([]);
    const [detail, setDetail] = useState(null);

    const getData = () => {
        client.get('/orders?token=' + user.token).then((rs) => {
            setOrders(s => rs.data.data.splice(0, 100));
        }).catch(e => {
            if (e.response && e.response.data) {
                toast.error(e.response.data.errors[0]);
            } else {
                toast.error('Kết nối máy chủ thất bại, vui lòng thử lại');
            }
        });
    }
    useEffect(() => {
        if (!user.token) {
            navigate(HOME + '/');
        }
        getData();
    }, []);

    return (
        <main>
            <section className="clear--padding">
                <div className="container">
                    <PageHeader title='History' slug='' last='Orders' />
                </div>
            </section>
            <section>
                <div className="container">
                    <table className="table scroll border cart-manager align--center">
                        <thead>
                            <tr className="align--center">
                                <th>Order ID</th>
                                <th>Fullname</th>
                                <th>Phone</th>
                                <th>Address</th>
                                <th>TOTAL</th>
                                <th>Status</th>
                                <th>Detail</th>
                            </tr>
                        </thead>
                        <tbody>
                            {orders.length > 0 && (
                                orders.map((o, i) => {
                                    return (
                                        <tr key={i}>
                                            <td>{o._id}</td>
                                            <td>{o.fullname}</td>
                                            <td>{o.phone}</td>
                                            <td>{o.address}</td>
                                            <td>{o.totalPrice.toLocaleString()} VNĐ</td>
                                            <td>{o.status}</td>
                                            <td>
                                                <button className="btn default" onClick={() => setDetail(s => o)}>View</button>
                                            </td>
                                        </tr>
                                    )
                                })
                            )}
                        </tbody>
                    </table>
                    {orders.length === 0 && (
                        <div className="pd border align--center">Chưa có đơn hàng nào được tạo</div>
                    )}
                    {detail && (
                        <Popup close={() => setDetail(null)}>
                            <OrderDetail data={detail} />
                        </Popup>
                    )}
                </div>
            </section>
            <div className="gap--50"></div>
            <div className="gap--50"></div>
        </main>
    )
}