// RENDER: PAGE HEADER
// Nhận vào:
//     -- props.title : Tiêu đề của trang
//     -- props.slug : Các link parent ( chưa được triển khai ở project này )
//     -- props.last : Tên trang hiện tại
// Callback: Không

// IMPORT
import { Link } from 'react-router-dom';
import { HOME } from 'config';

import './style.scss';
export default function PageHeader(props) {
    return (

        <div className='page-title'>
            <div className='flex x--between y--center'>
                <div className='col'>
                    <h2>{props.title}</h2>
                </div>
                <div className='col--min'>
                    <div className='slug-link'>
                        <Link to={HOME + '/'} >Home</Link>
                        <span>{props.last}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}